<template>
  <nav id="nav">
	<ul>
	  <li>
        <h2>编辑器</h2>
        <ul class="subnav">
          <li>
	        <router-link :to="menu.emacsKeyboard">
              Emacs常用命令
            </router-link>
          </li>
          <!--<li>
            <router-link to="">
              Vim常用命令
            </router-link>
          </li>
          <li>
            <router-link to="">
              VS Code快捷键
            </router-link>
          </li>-->
        </ul>
	  </li>
	  <li>
        <h2>纯文本文档语言</h2>
        <ul class="subnav">
          <li>
            <router-link :to="menu.orgmodeSyntax">
              Org Mode 语法
            </router-link>
          </li>
          <li>
            <router-link :to="menu.markdownSyntax">
              Markdown 语法
            </router-link>
          </li>
          <!-- <li>
            <router-link to="">
              TeX
            </router-link>
          </li> -->
        </ul>
	  </li>
      <li>
        <h2>链接</h2>
      	<ul class="subnav">
      	  <li>
	        <router-link :to="menu.link">网站链接</router-link>
          </li>
      	</ul>
      </li>
	</ul>
  </nav>
</template>

<script>
  export default {
    name: 'SideMenu',
    data () {
      return {
        menu: {
          link: '/link/index.html',
          emacsKeyboard: '/editor/emacs-keyboard-shortcuts.html',
          orgmodeSyntax: '/plain-text/org-mode-syntax.html',
          markdownSyntax: '/plain-text/markdown-syntax.html'
        }
      }
    }
  }
</script>
<style scope>
  #nav {
    width: 300px;
	height: 100%;
    position: fixed;
	top: 0;
	left: 0;
	z-index: 99;
	background-color: #66cc66;
    padding: .8rem;
    padding-top: 60px;
    overflow-y: auto;
    overflow-x: hidden;
  }
  #nav a {
    text-decoration: none;
    color: #FFFFCC;
    font-size: .8rem;
    line-height: 1.1rem;
  }
  #nav h2 {
    font-size: .85rem;
    color: #FFFFCC;
    font-weight: normal;
    padding: .4rem 0;
  }
  .subnav {
    padding-left: .8rem;
  }
  #nav .subnav .router-link-active {
    color: #CC6633;
  }
</style>



